
.fade-leave-active,.fade-2-enter-active,.fade-2-leave-active,.fade-3-enter-active,.fade-3-leave-active,.fade-4-enter-active,.fade-4-leave-active{transition:transform 0.3s
}
.fade-leave-active,.fade-2-enter,.fade-2-leave-active,.fade-3-enter,.fade-3-leave-active,.fade-4-enter,.fade-4-leave-active{transform:translateZ(0px) rotateY(-90deg);-webkit-transform:translateZ(0px) rotateY(-90deg)
}
.card-item{top:0;left:0;right:0;bottom:0;width:90%;height:85%;margin:auto;padding:40px 10px 60px;border-radius:4px;position:absolute;box-sizing:border-box;background-color:#fff
}
.card-item.card-start{height:80%;color:#999;padding:120px 20px 60px
}
.card-item.card-start .slogan-wrap{width:100%;position:absolute;top:20px;right:0;left:0;margin:auto;text-align:center
}
.card-item.card-start .slogan-wrap .logo{width:40px
}
.card-item.card-start .slogan-wrap .card-title{margin-top:0px;font-size:18px
}
.card-item.card-start .section{padding-top:0
}
.card-item.card-start .card-content{position:absolute;top:0;left:0;right:0;bottom:0;width:250px;text-align:justify;height:300px;margin:auto;font-size:15px;letter-spacing:2px;line-height:1.5
}
.card-item.card-start .card-content p{margin-bottom:15px;text-align:left
}
.card-item.card-start .card-footer{bottom:20px
}
.card-item.card-start .card-footer .button{letter-spacing:2px;width:230px
}
.card-item .card-footer{bottom:10px;width:90%;left:0;right:0;margin:auto;position:absolute;text-align:center
}
.card-item .card-footer .tip{font-size:16px
}
.card-item .card-footer .tip a{color:#53c295
}
.card-item .card-footer .button{height:40px;width:45%;line-height:40px;display:inline-block;border-radius:2px;background-color:#53c295;color:#fff;cursor:pointer
}
.card-item .card-footer .button.prev{margin-right:10px
}
.card-item .card-footer .button.disable{color:#999;background-color:#eee
}
.card-item .card-footer .button:hover{background-color:#42ba89
}
.card-item .card-footer .button:active{background-color:#3ba77b
}
.card-item.card-end{padding-top:20px
}
.card-item.card-end .button{color:#fff;height:40px;line-height:40px;border-radius:4px;background-color:#53c295;display:inline-block;position:absolute;bottom:20px;width:80%;left:0;right:0;margin:auto;text-align:center
}
.card-item.card-end .button:active{background-color:#3ba77b
}
.card-item.card-end .answer-item{padding-top:6px !important;padding-right:10px !important
}
.card-item.card-end .answer-item input{height:30px !important
}
.card-item .progress-wrap{position:absolute;top:15px;left:0;right:0;margin:auto;width:90%;height:6px;margin-bottom:10px;border-radius:2px;background-color:#eee
}
.card-item .progress-wrap .progress-bar{background-color:#53c295;height:100%;border-radius:2px;transition:width ease 0.3s
}
.section{height:100%;padding-top:30px;position:relative;box-sizing:border-box
}
.section.has-title{padding-top:70px
}
.section.has-title .question-title{height:30px;line-height:30px;position:absolute;top:30px;font-size:16px;color:#53c295;padding-left:4px
}
.section.has-title .question-title span{display:inline-block;height:30px;line-height:30px;vertical-align:bottom
}
.section.has-title .question-title input{border:none;outline:none;height:30px;width:120px;font-size:18px;margin-left:4px;color:#53c295;box-sizing:border-box;border-bottom:2px solid #53c295
}
.section .section-title{position:absolute;top:-6px;font-size:20px;font-weight:bolder;line-height:30px;height:30px;color:#666
}
.section .section-title .title-tip{font-size:14px
}
.question{height:100%;overflow:auto;-webkit-overflow-scrolling:touch;box-sizing:border-box
}
.question::-webkit-scrollbar{width:4px
}
.question::-webkit-scrollbar-thumb{background-color:#ddd
}
.question::-webkit-scrollbar-track{border:1px solid #eee
}
.question.input .answer-item{border:none !important
}
.question.input .answer-item .item-title{color:#666
}
.question.input .radio-input-wrap{margin-top:10px;width:45%;display:inline-block;position:relative;box-sizing:border-box;height:30px;line-height:30px;font-size:16px;color:#999
}
.question.input .radio-input-wrap input[type=radio]{height:20px;width:20px;top:6px;margin-right:5px;position:relative;-webkit-appearance:none;outline:none;background-color:transparent
}
.question.input .radio-input-wrap input[type=radio]::before{left:0;top:0px;z-index:2;content:'';width:16px;height:16px;position:absolute;box-sizing:border-box;border-radius:50%;border:2px solid #53c295
}
.question.input .radio-input-wrap input[type=radio]:checked+label{color:#53c295
}
.question.input .radio-input-wrap input[type=radio]:checked::after{left:3px;top:3px;z-index:2;content:'';width:10px;height:10px;position:absolute;box-sizing:border-box;border-radius:50%;background-color:#53c295
}
.question.input input[type=text]{height:40px;border:none;outline:none;width:100%;font-size:20px;color:#53c295;border-bottom:2px solid #53c295
}
.question.input .input-tip{font-size:20px;color:#53c295;position:absolute;bottom:12px;right:40px
}
.question.padding-big .answer-item{padding-top:20px;padding-bottom:20px
}
.question .answer-item{padding:12px 40px 12px 10px;border-top:1px solid #eee;position:relative;border-radius:4px;display:block
}
.question .answer-item .label-tip{font-size:12px;color:#666
}
.question .answer-item .iconfont{font-size:0
}
.question .answer-item input[type=checkbox]{opacity:0
}
.question .answer-item.active{background-color:#53c295;color:#fff
}
.question .answer-item.active .label-tip{color:#fff
}
.question .answer-item.active i.iconfont{position:absolute;top:0;bottom:0;margin-top:auto;margin-bottom:auto;right:10px;font-size:14px;text-align:center;line-height:20px;height:20px;width:20px;background-color:#fff;border-radius:50%;color:#53c295
}
.question .answer-item.active i.iconfont.square{border-radius:0
}
.question .answer-item:last-child{border-bottom:1px solid #eee
}



